import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers, RequestMethod } from '@angular/http';
import { User } from './user.model';
import { Role } from './role.model';
import { ConnectionService } from './connection.service';
import { map } from 'rxjs/operators';
import { HttpHeaders } from '@angular/common/http';

@Injectable()
export class UserService {
  selectedUser: User;
  isLogging : boolean = false;
  usersList: User[];
  rolesList: Role[];
  activeUser : User;
  constructor(
    private connectionService: ConnectionService,
    private http: Http
  ) { }
  readonly rootUrl = this.connectionService.rootUrl;

  registerUser(user: User, roles: any) {
    const body = {
      UserName: user.username,
      Password: user.password,
      Email: user.email,
      FirstName: user.firstName,
      LastName: user.lastName,
      Roles: roles
    }
    console.log(body);
    var reqHeader = new Headers({ 'Content-Type': 'application/json', 'No-Auth': 'True' });
    return this.http.post(this.rootUrl + '/api/User/Register', body, { headers: reqHeader }).pipe(map(res => res.json()));
  }

  userAuthentication(userName, password) {
    var data = "username=" + userName + "&password=" + password + "&grant_type=password";
    var reqHeader = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded', 'No-Auth': 'True' });
    return this.http.post(this.rootUrl + '/token', data, { headers: reqHeader }).pipe(map(res => res.json()));
  }

  getUserClaims() {
    return this.http.get(this.rootUrl + '/api/GetUserClaims').pipe(map(res => res.json()));
  }

  getAllRoles() {
    var reqHeader = new Headers({ 'No-Auth': 'True' });
    return this.http.get(this.rootUrl + '/api/GetAllRoles', { headers: reqHeader }).pipe(map(res => res.json()));
  }

  roleMatch(allowedRoles): boolean {
    var isMatch = false;
    var userRoles: string[] = localStorage.getItem('userRoles').split(',');
    allowedRoles.forEach(element => {
      if (userRoles.indexOf(element) > -1) {
        isMatch = true;
        return false;
      }
    });
    return isMatch;
  }

  postUser(user: User) {
    var body = JSON.stringify(user);
    console.log(`${body}`);
    var headerOptions = new Headers({ 'Content-Type': 'application/json' });
    headerOptions.append('Authorization', `Bearer ${localStorage.getItem('userToken')}`);
    var requestOptions = new RequestOptions({ method: RequestMethod.Post, headers: headerOptions });
    return this.http.post(this.rootUrl + '/api/CreateUser', body, requestOptions).pipe(map(res => res.json()));
  }

  putUser(id, user) {
    var body = JSON.stringify(user);
    var headerOptions = new Headers({ 'Content-Type': 'application/json', "Authorization": "Bearer " + localStorage.getItem('userToken') });
    var requestOptions = new RequestOptions({ method: RequestMethod.Put, headers: headerOptions });
    return this.http.put(this.rootUrl + '/api/UpdateUser/' + id, body, requestOptions).pipe(map(res => res.json()));
  }

  getAllUsers() {
    this.http.get(this.rootUrl + '/api/GetAllUsers')
      .pipe(map((data: Response) => {
        return data.json() as User[];
      })).toPromise().then(x => {
        this.usersList = x;
      })
  }
  
  getUser(id) {
    return this.http.get(this.rootUrl + '/api/GetUser/' + id)
      .pipe(map(res => res.json() as User))
  }

  getRoles() {
    this.http.get(this.rootUrl + '/api/GetAllRoles')
      .pipe(map((data: Response) => {
        return data.json() as Role[];
      })).toPromise().then(x => {
        this.rolesList = x;
      })
  }

  deleteUser(id: number) {
    var headerOptions = new Headers({ 'Content-Type': 'application/json', "Authorization": "Bearer " + localStorage.getItem('userToken') });
    //var header = new HttpHeaders({"Authorization" : "Bearer " + localStorage.getItem('userToken')});
    var requestOptions = new RequestOptions({ method: RequestMethod.Delete, headers: headerOptions });
    return this.http.delete(this.rootUrl + '/api/DeleteUser/' + id, requestOptions).pipe(map(res => res.json()));
  }

}